import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { MatDialog, MatDialogConfig } from '@angular/material';
import { MatDialogComponent } from './../mat-dialog/mat-dialog.component';
import { DataService } from './../../services/data.service';

@Component({
  selector: 'app-report-uf',
  templateUrl: './report-uf.component.html',
  styleUrls: ['./report-uf.component.scss']
})
export class ReportUfComponent implements OnInit {

  report=[];
  

  constructor(private http: HttpClient,public dialog: MatDialog, private dataService: DataService) {}

  ngOnInit() {
    this.refreshbutton()   
  }
  public refreshbutton() {
    this.http.get("http://127.0.0.1:5000/api/report_uf")
    .subscribe((data:any) => {  
      this.report = data;
      
    });
    
  }
   refreshReport() {
    return new MatTableDataSource<any>(this.report);
  }

  openModal(element) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.autoFocus = true;
    dialogConfig.width = '1000px';
    // dialogConfig.maxHeight = '650px';
    dialogConfig.data = {
      id: 1,
      title: 'Report',
      data: element
    };
    const dialogRef = this.dialog.open(MatDialogComponent, dialogConfig);
    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
    });
  }

  download(text) {
  const data = new Blob([text], {type: 'text/plain'});
  const url = window.URL.createObjectURL(data);

  const a = document.createElement('a');
  document.body.appendChild(a);
  a.href = url;
  a.download = "report.csv";
  a.click();
  setTimeout(() => {
    window.URL.revokeObjectURL(url);
    document.body.removeChild(a);
  }, 0)  }
  
}

