import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { element } from '@angular/core/src/render3';
import { DataService } from './../../services/data.service';
import { HttpClient } from '@angular/common/http';
import { environment } from './../../../environments/environment';

@Component({
  selector: 'app-remote-cmd-executor',
  templateUrl: './remote-cmd-executor.component.html',
  styleUrls: ['./remote-cmd-executor.component.scss']
})
export class RemoteCmdExecutorComponent implements OnInit {
  baseAPI = environment.baseAPI;
  runStatus = ""
  progress = 0
  running = false
  status = ''
  remCmdExeForm: FormGroup;
  executionOutput = {};
  hide = true;
  enableHide = true;
  output=""
  
 
  ip=''
  ipAddresses=[]  
  command = ''
  commands=[]
  jumpServerIP=""
  jumpServerUserName=""
  jumpServerPassword=""
  Provider=""
  deviceUserName=""
  devicePassword=""
  
  
 
  constructor(private http: HttpClient,private formBuilder: FormBuilder, private dataService: DataService) { }

  ngOnInit() {
    this.remCmdExeForm = this.formBuilder.group({
      IP_Hostname: ['', [Validators.required]],
      Commands: ['', [Validators.required]],    

      jumpServerIP: [''],     
      jumpServerUserName: [''],
      jumpServerPassword: [''],
     
      deviceUserName: ['', [Validators.required, Validators.maxLength(200)]],
      devicePassword: ['', [Validators.required, Validators.maxLength(200)]],
      Provider: ['', [Validators.required]],
      deviceConnectionType: ['telnet'],
      isJumpserver: [false]
    });
    var nextLine = '\n';
    this.ipAddresses.forEach((element, index) => {
      this.ip = this.ip + element + nextLine;
      if(index == this.ipAddresses.length-2){
        nextLine = '';
      }
    })
    
    nextLine = '\n';
    this.commands.forEach((element,index)=> {
      this.command = this.command + element + nextLine;
      if(index == this.commands.length-2){
        nextLine = '';
      }
    })
  
    this.remCmdExeForm.get('isJumpserver').valueChanges.subscribe(
      (mode) => {
        if (mode === true) {
          this.remCmdExeForm.get('jumpServerIP').setValidators([Validators.required, Validators.maxLength(200)]);
          this.remCmdExeForm.get('jumpServerUserName').setValidators([Validators.required, Validators.maxLength(200)]);
          this.remCmdExeForm.get('jumpServerPassword').setValidators([Validators.required, Validators.maxLength(200)]);
      }
      else if (mode === false) {          
          this.remCmdExeForm.get('jumpServerIP').clearValidators();
          this.remCmdExeForm.get('jumpServerUserName').clearValidators();
          this.remCmdExeForm.get('jumpServerPassword').clearValidators();
      }
      
      this.remCmdExeForm.get('jumpServerIP').updateValueAndValidity();
      this.remCmdExeForm.get('jumpServerUserName').updateValueAndValidity();
      this.remCmdExeForm.get('jumpServerPassword').updateValueAndValidity();
      })
  
  }

  

  onclick() {
    
   
    this.ipAddresses = this.ip.split(/\n/);
    var finalIp = [];
    this.ipAddresses.forEach((element)=> {
      if(element != ''){
        finalIp.push(element);
      }
    });
    this.ipAddresses = finalIp;
    this.commands = this.command.split(/\n/);
    var finalCommand = [];
    this.commands.forEach((element)=> {
      if(element != ''){
        finalCommand.push(element);
      }
    });
    this.commands = finalCommand;
    if (this.remCmdExeForm.invalid) {
      return;
    }
    
    let input={
      "jmpServerIp":this.jumpServerIP,
      "jmpServerUsername":this.jumpServerUserName,
      "jmpServerPassword":this.jumpServerPassword,
      "OEM":this.Provider,
      "deviceUsername":this.deviceUserName,
      "devicePassword":this.devicePassword,
      "deviceAddresses":this.ipAddresses,
      "commands":this.commands,
      "deviceConnectionType":this.remCmdExeForm.controls['deviceConnectionType'].value,
      "isJumpServer":this.remCmdExeForm.controls['isJumpserver'].value
    }
    this.running = true
    this.executionOutput = {}
    //this.http.post(this.baseAPI + "api/remote-execution", input)
    this.http.post("http://127.0.0.1:5000/api/remote-execution", input)
    .subscribe((data:any) => {
      // this.running = false
        this.executionOutput = data;    
        this.status = "Success";
        this.runStatus = "Executed Successfully" 
        this.running=false;
        
    },(error:any)=>{
        this.status = "Failure";
        this.runStatus = "Executed Failed";
        this.running=false;
    });
    
    if(this.runStatus=="Executed Successfully")
    {
      this.onclickReset();
    }

   
    this.running = true
    this.progress = 100
    this.runStatus = "";

    /*setTimeout(() => {   
      this.status = "Success";
      this.runStatus = "Executed Successfully"
      var flag = false, index = 0;
      this.dataService.report.forEach((element, elementIndex)=> {
        if(element.name == 'Remote Commands Executor') {
          flag = true;
          index = elementIndex;
        }
      })
      
    }, 14000);*/
   


  }

  onclickReset() {
    this.running = false
    this.progress = 0
    this.runStatus = ""
  }

}
