import { Component, OnInit } from '@angular/core';
import { MatTableDataSource, MatSort } from '@angular/material';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { DataService } from './../../services/data.service';

@Component({
  selector: 'app-cfg-audit',
  templateUrl: './cfg-audit.component.html',
  styleUrls: ['./cfg-audit.component.scss']
})
export class CfgAuditComponent implements OnInit {

  dataSource: MatTableDataSource<any>;
  running = false;
  ip = '';
  cfgAuditForm: FormGroup;
  targetPasswordHide = true;
  findings = [
    {
      id: 1,
      ip: "172.123.65.21",
      finding: "This will be the finding for this IP"
    }, {
      id: 2,
      ip: "171.423.68.56",
      finding: "This will be the finding for this IP"
    }, {
      id: 3,
      ip: "172.748.73.62",
      finding: "This will be the finding for this IP"
    }, {
      id: 4,
      ip: "177.847.84.21",
      finding: "This will be the finding for this IP"
    }]
  ipAddresses : any;
  status = '';
  runStatus = '';
  constructor(private formBuilder: FormBuilder, private dataService: DataService) { }

  ngOnInit() {

    this.cfgAuditForm = this.formBuilder.group({
      IP_Hostname: ['', [Validators.required]],
      Device: ['', [Validators.required]],
      UserName: ['', [Validators.required, Validators.maxLength(200)]],
      Password: ['', [Validators.required, Validators.maxLength(200)]],
      DeviceType: ['', [Validators.required]],
    });

    this.dataSource = new MatTableDataSource<any>(this.findings);
    var nextLine = '\n';
    this.findings.forEach((element, index) => {
      this.ip = this.ip + element.ip + nextLine;
      if(index == this.findings.length-2){
        nextLine = '';
      }
    })
  }

  onclick() {
    if (this.cfgAuditForm.invalid) {
      return;
    }
    this.ipAddresses = this.ip.split(/\n/);
    this.ipAddresses.forEach((element, index) => {
      if (this.findings[index] == null) {
        this.findings.push({
          id: index,
          ip: element,
          finding : "This will be the finding for this ip"
        })
      } else {
        this.findings[index].ip = element;
      }      
  })
  if(this.ipAddresses.length < this.findings.length) {
    for(var index = this.ipAddresses.length; index < this.findings.length; index++) {
      this.findings[index].ip = '';
    }
  }
    var finalFindings = [];
    this.findings.forEach((element) => {
      if (element.ip != '') {
        finalFindings.push(element);
      }
    });
    this.findings = finalFindings;
    this.status = '';
    setTimeout(() => {  
      this.running = true;
      this.dataSource = new MatTableDataSource<any>(this.findings); 
      this.status = "Success";
      this.runStatus = "Executed Successfully"
      var flag = false, index = 0;
      this.dataService.report.forEach((element, elementIndex)=> {
        if(element.name == 'Remote Commands Executor') {
          flag = true;
          index = elementIndex;
        }
      })
      if(flag) {
        this.dataService[index].runBy = this.cfgAuditForm.controls['UserName'].value;
        this.dataService.report[index].lastRunDate = new Date();
        this.dataService.report[index].status = this.status;
        this.ipAddresses.forEach(element=> {
          this.dataService.report[index].details.push({
            ip: element,
            log: "This.wil be the log for " + element
          })
        })
      } else {
        this.dataService.report.push({
          id : this.dataService.report.length + 1,
          name: "Configuration Audit",
          description: "This will be the description for Configuration Audit",
          createdDate: new Date(),
          commands : [],
          details: [],
          lastRunDate: new Date(),
          runBy: this.cfgAuditForm.controls['UserName'].value,
          status : this.status
        })
        this.ipAddresses.forEach(element=> {
          this.dataService.report[this.dataService.report.length-1].details.push({
            ip: element,
            log: "This.wil be the log for " + element
          })
        })
      }  
    }, 2000);  
  }

  download() {
    let link = document.createElement("a");
    link.download = "filename";
    link.href = "assets/doc/config_audit.txt";
    link.click();
  }

  onClickReset() {
    this.running = false;
  }

}
