  assignConfigurationForm() {
    this.configurationFormGroup = this.formBuilder.group({
      template: this.formBuilder.array([]),
      provider: [this.provider, [Validators.required]],
      customParent: this.formBuilder.array([]),
      IP_Hostname: [this.ip, [Validators.required]],
      OutputCommand: [this.outputCommand,[Validators.required]]
    });
    const template = <FormArray>this.configurationFormGroup.controls['template'];
    this.templates.forEach(element => {
      template.push(this.createTemplateFormGroup(element));
    })
    const customParent = <FormArray>this.configurationFormGroup.controls['customParent'];
    var index = 0;
    this.customBuild.forEach(element => {
      customParent.push(this.createParentFormGroup(element));
      const childData = <FormArray>(<FormGroup>customParent.controls[index]).controls['customChild'];
      var childIndex = 0;
      element.child.forEach(child => {
        childData.push(this.createChildFormGroup(child));
        const parameter = <FormArray>(<FormGroup>childData.controls[childIndex]).controls['parameters'];
        if (child.checked) {
          child.technologyProvider.forEach(technologyProvider => {
            technologyProvider.parameter.forEach(parameterElement => {
              parameter.push(this.createAnotherParameterFormGroup(parameterElement));
            })
          });
        }
        childIndex++;
      });
      index++;
    });
    console.log(this.configurationFormGroup);
  }

  createTemplateFormGroup(element) {
    return this.formBuilder.group({
      templateChecked: [element.checked]
    });
  }

  createParentFormGroup(element) {
    return this.formBuilder.group({
      parent: [element.checked],
      customChild: this.formBuilder.array([])
    });
  }

  createChildFormGroup(element) {
    return this.formBuilder.group({
      child: [element.checked],
      parameters: this.formBuilder.array([])
    });
  }

  createParameterFormGroup(element) {
    return this.formBuilder.group({
      parameterValue: [element.value, [Validators.required]]
    });
  }

  createAnotherParameterFormGroup(element) {
    return this.formBuilder.group({
      parameterValue: [element.value]
    });
  }

